.App {
  text-align: center;
  user-select: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  position: relative;
  top: 0;
  left: 0;
}

.Gear {
  height: 15vmin;
  pointer-events: none;
  position: absolute;
  left: 12.2vmin;
  top: 28vmin;
}
.Gear2 {
  height: 12vmin;
  position: absolute;
  pointer-events: none;
  left: 22vmin;
  top: 20vmin;
}

.parent {
  position: relative;
  top: 0;
  left: 0;
}

.Header1 {
  color: #ee6c4d;
  font-family: "Courier New", Courier, monospace;
}

.logo404 {
  height: 60vmin;
  pointer-events: none;
  position: relative;
}

.Par1 {
  color: #98c1d9;
  font-family: Arial, Helvetica, sans-serif;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 2s alternate;
  }
  .Gear {
    animation: Gear-spin infinite 10s linear;
  }
  .Gear2 {
    animation: Gear2-spin infinite 10s linear;
  }
}

.App-header {
  background-color: #293241;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(-4deg) scale3d(1.05, 1, 1);
  }
  to {
    transform: rotate(1deg) scale3d(1, 1.07, 1);
  }
}

@keyframes Gear-spin {
  from {
    transform: rotate(15deg);
  }
  to {
    transform: rotate(375deg);
  }
}
@keyframes Gear2-spin {
  from {
    transform: rotate(360deg);
  }
  to {
    transform: rotate(0deg);
  }
}
